import { Component, OnInit} from '@angular/core';
import { WizardState} from '../services/app.wizardState.service';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable'; 
import { importJsonData,getJSONdata } from  '../../../shared/shared'
import { gavePct,gaveFradragLevel} from '../data/gaveAfgiftFixedRules';
import { gaveData } from '../infrastructure/interfacesGave';
import { gaveAfgiftBeregninger } from '../services/gaveAfgiftCalculation.service';

interface basic<T> {
    id:string
    children?:basic<T>[]
}

interface languageText {
    id:string,
    da:string,
    en?:string,
    children?:languageText[]
}

interface detailed {
    id:string,
    description:string
}


@Component({

    selector: 'my-app',

    template:`
        <div class = "skts-wizard gave-afgift">
        
            <h3>Pengegave til et familiemedlem</h3>

            <p>Intro ...</p>

            <selector 
                [options]   = "familyNames | async"
                [label]     = "txt('giverTil') | async"
                [(value)]   = "familyId">
            </selector>

            <div *ngIf = "detailedDescription().length > 0" class = "group-details">
                <input type = 'button' value = 'Detaljer om hvem dette omfatter' class = "asLink" (click) = "showDetail = !showDetail"> 
                <div *ngIf = "showDetail == true">{{detailedDescription()}}</div>
            </div>         
           
            <div *ngIf = "familyId == 'svigerboern'">
                <bootstrapRadio
                    [options] = "svigerboernNames | async"
                    [(value)] = "svigerbarnIlive"  
                    [label]   = "txt('Svigerboern_label') | async"    
                ></bootstrapRadio>
            </div>

            <div *ngIf = "familyId == 'aegtefaelle'">
                <p>Du kan frit overfre kontantgaver til din gteflle</p>
            </div>

            <div *ngIf = "familyId == 'andre'">
                <p>Giver en gaver til andre - herunder - sskende - skal den der modtager gaven angive dette som anden indkomst i rsopgrelsen</p>
            </div>

            <div  class = "information-on-rules" *ngIf = "(afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async) > 0">

                <h3>
                    Det kan du give skattefrit i {{year}} 
                    <input type = 'button' [value] = "txtChangeYear() | async" class = "asLink extra-small" (click) = "yearToggle = !yearToggle;year = year + toggleYear();updatingModel()">
                </h3>

                <p>
                    Du kan give <span class = "focus-on-number"> {{afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async | tusindtal }} kr. </span>  
                    {{tilRelation() | async}}. i {{year}}
                </p>

                <p *ngIf = "isDual()">
                    Du kan give {{beregnService.fradrag | tusindtal }} kr. til hver, s de samlet fr {{(beregnService.fradrag * 2) | tusindtal}} kr.
                </p>

                <h3>Giver du mere?</h3>
                <p>
                    Giver du mere end {{afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async | tusindtal }} kr., er 
                    gaveafgiften <span class = "semi-large">{{skatPctAfGaveBeloeb() | decimalDK}} % </span> af belbet over {{afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async | tusindtal }} kr.
                    Du kan beregne afgiften her.
                </p>

            </div>

            <div class = "skts-foldout" *ngIf = "(afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async) > 0">
                <button type = "button" class = "skts-foldout-clickable" (click) = "calculateAfgift = !calculateAfgift">
                    Beregn gaveafgift, hvis du giver mere <span class = "arrow pull-right">A</span>
                </button>

                <div *ngIf = "calculateAfgift" class = "foldout-body">

                    <p class = "info-section">
                        Indtast enten (1) hvor meget du nsker den du giver til skal have eller (2) hvor mange penge du vil give. 
                        <br/><br/>
                        Afgiften afhnger af hvem der betaler den. Det er en konomisk fordel, hvis giveren betaler afgiften, da denne fr mulighed for at trkke dette belb fra inden, der skal betales gaveafgift.   
                    </p>   
            
                    <div class = "row">
                        <div class = "col-xs-12">
                            <label for = "giverBeloeb">Du giver i gave (kr.) </label>
                            <div class = "skts-postfix-kr">
                                <input [(readableDigitFormat)] = "beregnService.giverBeloeb" class = "form-control" id = "giverBeloeb" />
                            </div>
                        </div>
                    </div>

                    <div class = "row">
                        <div class = "col-xs-12">
                            <label for = "modtagerBeloeb">Modtager fr/skal have</label>
                            
                            <div class = "skts-postfix-kr">
                                <input [(readableDigitFormat)] = "beregnService.modtagerFaar" class = "form-control" id = "modtagerBeloeb" />
                            </div>
                        </div>
                    </div>

                    <div class = "row">
                        <div class = "col-xs-12">
                            <input type = "checkbox" [(ngModel)] = "beregnService.giverBetalerAfgift"  id = "whopays" /> 
                            <label for = "whopays" >Giver betaler gaveafgift</label>
                        </div>
                    </div>
                    
                    <div class = "row" *ngIf = "beregnService.gaveafgiftUser() > 0">
                        <div class = "col-xs-12">
                            <div class = "text-center">
                                <p>Gaveafgift, der skal betales</p>
                                <p class = "result">
                                    <strong class = "skts-row-result skts-row-result--large">{{beregnService.gaveafgiftUser() | tusindtal}} kr.</strong>
                                </p>          
                            </div>

                            <h4><i>Det gr du:</i></h4>
                            
                                Giver du dette belb, skal du indberette det til SKAT ved at indsende en blanket. 
                                Du skal bl.a. bruge flgende oplysninger under afsnit 6 vedrrende gaveafgiftsberegning

                                <ol class = "variables-on-form" type = "a">
                                    <li>Gavebelb: <i>{{beregnService.giverBeloeb | tusindtal}} kr.</i></li>
                                    <li>Afgiftsfrit belb: <i>{{beregnService.fradrag | tusindtal }} kr.</i></li>
                                    <li>Afgiftsgrundlag: <i>{{beregnService.afgiftsgrundlag() | tusindtal}} kr.</i></li>
                                    <li>Gaveafgift: <i>{{beregnService.gaveafgiftUser() | tusindtal}} kr.</i></li>

                                </ol>
                                               
                            <a [href] = "urlForm" class = "action-link" target = "_blank">bn og udfyld kontantgaveblanket</a>
                        </div>
                    </div>

                    <div class = "row" *ngIf = "beregnService.afgiftBeloeb() === 0"> 
                         <div class = "col-xs-12">
                            <div class = "text-center">
                                <p>Afgiftsfrit op til <span class = "focus-on-number"> {{afgiftFritBeloeb(year,afgiftsbeloebGeneral()) | async | tusindtal }} kr.</span></p>
                            </div>         
                        </div>
                    </div>
                </div>
            </div>
        </div>
    
    `

})

export class appMain extends importJsonData  {

    constructor (public http:Http,private data:getJSONdata) {
        super(http)
    }

    ngOnInit () {
       
       this.setArrayObservables('app/txt.json','detailedDescription','da')
       this.familyId = 'boernAfkom'
       this.beregnService.giverBetalerAfgift = true
       this.setYear()
    }

    familyId_:string = '';
    svigerbarnIlive_:string = '';
    afgiftFritBeloebUI:number; 
    year:number = new Date().getFullYear();
    showDetail:boolean = false;
    calculateAfgift:boolean = false;
    production:boolean = false;
    urlText:string = 'app/txt.json';
    urlData:string = 'app/gaveAfgiftSatser.json';
    urlForm:string = 'http://www.skat.dk/getFile.aspx?Id=132365';
    yearToggle:boolean = true;


    familyNames = this.textArrayIdMap('app/txt.json','mainCategories','da')
    svigerboernNames = this.textArrayIdMap('app/txt.json','svigerboern','da')

    beregnService:gaveAfgiftBeregninger = new gaveAfgiftBeregninger()

    setYear() {
        /* 

        looking for the newest year on server (JSON-ressource)
        in case we data is made available for next next year, year will still be current year
        in case all other cases we use the latest year available in data

         */
        let currentYear = new Date().getFullYear()

        this.data.fetch<gaveData>('app/gaveAfgiftSatser.json')
            .map(yearObj => yearObj.year)
            .reduce((prev,year) => {
                return (year > prev) ? year : prev
            },0)
            .subscribe(latestYearFromDatabase => {
                this.year = (currentYear <= latestYearFromDatabase) ? currentYear : latestYearFromDatabase
                this.updatingModel() 
            })
    }

    /* every time key properties change we need to manually update model */

    get familyId() {
        return this.familyId_;
    } 

    set familyId(val:string) {
        this.familyId_ = val;
        this.updatingModel()
    }

    get svigerbarnIlive() {
        return this.svigerbarnIlive_
    }

    set svigerbarnIlive(val:string) {
        this.svigerbarnIlive_ = val   
        this.updatingModel()
    }

    /* methods for calculation */

    getModtager() {
        return (this.familyId == 'svigerboern') ? this.svigerbarnIlive : this.familyId 
    }

    afgiftsbeloebGeneral() {
       let type = gaveFradragLevel.find(el => {
           return el.ids.indexOf(this.getModtager()) > -1
        })
       return (type) ? type.fradragType : '' 
    }

    afgiftFritBeloeb(year:Number,type:string):Observable<number> {
        return this.data.fetch<gaveData>('app/gaveAfgiftSatser.json')
            .find(yearObj => yearObj.year == year)
            .map(obj => obj[type])        
    }

    skatPctAfGaveBeloeb() {
        let type = gavePct.find(el => {
             return el.ids.indexOf(this.getModtager()) > -1    
        })
        return type ? type.gavePct : -1
    } 

    updatingModel() {
        this.beregnService.gaveAfgiftPct = this.skatPctAfGaveBeloeb()
        this.afgiftFritBeloeb(this.year,this.afgiftsbeloebGeneral()).subscribe(el => {
            this.beregnService.fradrag = el;
        })

    }

    /* methods for text in component */

    toggleYear() {
        return (this.yearToggle) ? 1 : -1
    }

    isDual() {
        let couple = ['foraeldre','bedsteforaeldre','stedforaeldre']
        return !!(couple.indexOf(this.getModtager()) > -1) 
    } 

    txtChangeYear() {

        return Observable.create((observer:any) => {
            this.txt('changeYear').subscribe(txt => {
                let txtYear = `${txt} ${this.year + ((this.yearToggle) ? -1 : 1)}`
                observer.next(txtYear)
            })
        })  
    }

   detailedDescription() {
        return this.getArrayValues('detailedDescription',this.familyId)
    }

    txt(txt:string):Observable<string> {
        return this.data.fetch<languageText>('app/txt.json')
            .find(txt => txt.id == 'textlayer')
            .map(obj => obj.children.find(sub => sub.id == txt))
            .map(obj => {return (obj) ? obj.da : ''})           
    }

    tilRelation():Observable<string> {
        return this.data.fetch<languageText>('app/txt.json')
            .find(txt => txt.id == 'tilRelation')
            .map(obj => obj.children.find(sub => sub.id == this.familyId))
            .map(obj => {return (obj) ? obj.da : ''})           
    }



}



